
/* Login Page Styles */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: url('/Reactprojects/image/login.jpg'); /* Replace with the correct image path */
  background-size: cover;
  background-position: center;
}

.login-content {
  background-color: rgba(255, 255, 255, 0.1); /* Semi-transparent overlay */
  padding: 40px 20px;
  max-width: 400px;
  text-align: center;
  border-radius: 8px;
}

.login-title {
  font-size: 2rem;
  font-weight: bold;
  color: white;
  margin-bottom: 30px;
}

.input-group {
  position: relative;
  margin-bottom: 20px;
}

.icon {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 1.2rem;
}

.login-input {
  width: 100%;
  padding: 12px 40px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  border-bottom: 2px solid #fff;
  transition: border 0.3s ease;
}

.login-input::placeholder {
  color: #ddd;
}

.login-input:focus {
  border-color: #007bff;
  outline: none;
  background-color: rgba(255, 255, 255, 0.3);
}

.login-btn {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.login-btn:hover {
  background-color: #0056b3;
}

/* Responsive Design */
@media (max-width: 600px) {
  .login-content {
    padding: 20px;
    max-width: 90%;
  }
}
